body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.app-frame {
  margin: 0;
  padding: 30px;
  box-sizing: border-box;
  width: 100vw;
  height: 100vh;
  background: #00cbcc;
}

.customers-actions {
  margin: 0;
  position: absolute;
  bottom: 20px;
  left: 30px;
  background: rgba(0, 0, 0, 0.2);
  padding: 10px;
  width: calc(100% - 80px);
}

.customers-actions button {
  background: white;
  min-width: 100px;
  height: 30px;
  margin: 5px;
  border-style: none;
  border-radius: 3px;
  font-weight: bolder;
}

.customers-actions button:hover {
  background: #f0f0f0;
  border-style: solid;
}

.customers-list .customer-list-item {
  width: 100%;
  background: #fff;
  transition: 500ms;
  height: 40px;
}

.customers-list .customer-list-item:nth-child(even) {
  background: #eee;
}

.customers-list .customer-list-item:hover {
  background: #84adad3a;
  border: 1px solid;
}

.customer-list-item .field {
  width: 160px;
  margin: 1px;
  padding: 4px;
  overflow: hidden;
  display: inline-block;
  line-height: 40px;
}
.customer-list-item .field:first-child {
  width: 70%;
  margin: 0;
  box-sizing: border-box;
}

.customer-list-item .field {
  width: 15%;
  margin: 0;
  box-sizing: border-box;
}

.customer-list-item .field:first-child > a {
  color: #222;
  text-decoration: none;
  font-weight: bolder;
}

.customer-list-item .field a {
  color: #666;
  text-decoration: none;
}

.app-frame p {
  color: white;
  font-size: 10px;
  font-style: italic;
}

.app-frame form {
  width: 100%;
  background: rgba(255, 255, 255, 0.5);
  padding: 20px;
  box-sizing: border-box;
  border-radius: 5px;
}

.app-frame form label {
  width: 100%;
  display: inline-block;
  margin-top: 10px;
  color: rgba(0, 0, 0, 0.7);
  font-size: 12px;
}

.app-frame form input {
  font-size: 18px;
  font-style: bolder;
}

.app-frame form span {
  color: red;
  margin: 5px;
  font-size: 10px;
  font-style: italic;
}

.app-header {
  border: 0;
  border-bottom: 1px;
  border-style: solid;
  border-color: white;
}

.app-header h1 {
  color: white;
}

.app-frame h2 {
  font-weight: lighter;
  font-size: 1.3em;
}

img {
  border-radius: 4px;
  padding-top: 20px;
  padding-bottom: 20px;
  overflow: hidden;
  height: 300px;
  margin: 0 auto;
  display: block;
}

.customer-data strong {
  width: 100%;
  display: inline-block;
  margin-top: 10px;
  color: #333;
  font-size: 11px;
}

.customer-data strong {
  font-size: 20px;
}

.App {
  text-align: left;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #019191;
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

